import React, {
  ChangeEvent,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Chip from '@mui/material/Chip';
import Container from '@mui/material/Container';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import FormControlLabel from '@mui/material/FormControlLabel';
import Grid from '@mui/material/Grid';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import TextField from '@mui/material/TextField';
import {graphql, PageProps} from 'gatsby';
import {sortBy} from 'lodash';

import CollegeDetailsCard from '../../components/CollegeDetailsCard';
import Layout from '../../components/layout';
import {CollegeDetailsProps} from '../../templates/college-details';

type DataProps = {
  allStrapiCollege: {
    nodes: CollegeDetailsProps[];
  };
  allStrapiCollegeType: {
    nodes: {
      name: string;
      id: string;
      college_type: {
        name: string;
        id: string;
      };
    }[];
  };
};

const COMMON_FILTER_OPTION = [
  {
    label : 'All Colleges',
    value : '0',
  },
];

const SORT_OPTIONS = [
  {
    label : 'Colleges Code - A to Z',
    value : '0',
  },
  {
    label : 'Colleges Name - A to Z',
    value : '1',
  },
  {
    label : 'Colleges Code - Z to A',
    value : '2',
  },
  {
    label : 'Colleges Name - Z to A',
    value : '3',
  },
];

// markup
const Colleges = ({
  data: {allStrapiCollege, allStrapiCollegeType},
}: PageProps<DataProps>) => {
  const [searchText, setSearchText] = useState('');
  const [isOpenFilterDialog, setOpenFilterDialog] = useState(false);
  const [filterRadioOption, setFilterRadioOption] = useState(COMMON_FILTER_OPTION[0]);
  const [filterOption, setFilterOption] = useState(COMMON_FILTER_OPTION[0]);
  const [filterOptions, setFilterOptions] = useState(COMMON_FILTER_OPTION);

  const [sortRadioOption, setSortRadioOption] = useState(SORT_OPTIONS[0]);
  const [sortOption, setSortOption] = useState(SORT_OPTIONS[0]);
  const [isOpenSortDialog, setOpenSortDialog] = useState(false);

  const radioFilterGroupRef = useRef<HTMLElement>(null);
  const radioSortGroupRef = useRef<HTMLElement>(null);

  useEffect(() => {
    const filterOptionsByCollege = [...COMMON_FILTER_OPTION];
    allStrapiCollegeType.nodes.map((collegeType) => {
      filterOptionsByCollege.push({
        label : collegeType.name,
        value : collegeType.id,
      });
    });
    setFilterOptions(filterOptionsByCollege);
  }, [allStrapiCollegeType.nodes]);

  const handleClickListItem = useCallback(() => {
    setOpenFilterDialog(true);
  }, []);

  const handleClickSortListItem = useCallback(() => {
    setOpenSortDialog(true);
  }, []);

  const handleClose = useCallback(() => {
    setOpenFilterDialog(false);
  }, []);

  const handleSortClose = useCallback((newValue?: string) => {
    setOpenSortDialog(false);
  }, []);

  const handleChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      setSearchText(event.target.value);
    },
    [setSearchText]
  );

  const filteredColleges = useMemo(() => {
    let results;
    if (filterOption.value !== '0') {
      setSearchText('');
      results = allStrapiCollege.nodes.filter(
        (college) => college.college_type.id === filterOption.value
      );
    } else {
      // filter branch by name or code
      results = allStrapiCollege.nodes.filter((branch) => {
        return (
          branch.name.toLowerCase().includes(searchText.toLowerCase()) ||
          branch.code.toString().includes(searchText)
        );
      });
    }
    // sort results by name or code using sortOption.value using lodash sortby
    if (sortOption.value === '0') {
      results = sortBy(results, 'code');
    }
    else if (sortOption.value === '1') {
      results = sortBy(results, 'name');
    }
    else if (sortOption.value === '2') {
      results = sortBy(results, 'code').reverse();
    }
    else if (sortOption.value === '3') {
      results = sortBy(results, 'name').reverse();
    }

    return results;
  }, [filterOption.value, allStrapiCollege.nodes, searchText, sortOption.value]);

  const handleEntering = useCallback(() => {
    if (radioFilterGroupRef.current != null) {
      radioFilterGroupRef.current.focus();
    }
  }, []);

  const handleSortEntering = useCallback(() => {
    if (radioSortGroupRef.current != null) {
      radioSortGroupRef.current.focus();
    }
  }, []);

  const handleCancel = useCallback(() => {
    setFilterRadioOption(filterOption);
    handleClose();
  }, [filterOption]);

  const handleOk = useCallback(() => {
    setFilterOption(filterRadioOption);
    handleClose();
  }, [filterRadioOption]);

  const handleSortCancel = useCallback(() => {
    handleSortClose();
    setSortRadioOption(sortOption);
  }, [sortOption]);

  const handleSortOk = useCallback(() => {
    handleSortClose();
    setSortOption(sortRadioOption);
  }, [sortRadioOption]);

  const handleFilterChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const selectedOption = filterOptions.find(
      (option) => option.value === event.target.value
    );
    if (selectedOption) {
      setFilterRadioOption(selectedOption);
    }
  };

  const handleSortChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const selectedOption = SORT_OPTIONS.find(
      (option) => option.value === event.target.value
    );
    if (selectedOption) {
      setSortRadioOption(selectedOption);
    }
  };

  return (
    <Layout headerTitle="Colleges">
      <Box>
        <TextField id="standard-search"
          label="Search College Name or Code"
          type="search"
          variant="filled"
          value={searchText}
          onChange={handleChange}
          fullWidth/>
      </Box>
      <Container>
        <Grid container
          direction="row"
          justifyContent="space-between"
          alignItems="center" sx={{my: 3}}>
          <Chip label={`${filteredColleges.length} Colleges Found`} size="small" />

          {/* <Stack direction="row" spacing={2}>
            <IconButton aria-label="filter" onClick={handleClickListItem}>
              <FilterAltIcon color="secondary" />
            </IconButton>
            <IconButton aria-label="sort" onClick={handleClickSortListItem}>
              <SortIcon color="secondary" />
            </IconButton>
          </Stack> */}
        </Grid>
      </Container>
      <CollegeDetailsCard colleges={filteredColleges} />

      <Dialog sx={{'& .MuiDialog-paper': {width: '80%', maxHeight: 435}}}
        maxWidth="xs"
        TransitionProps={{onEntering: handleEntering}}
        open={isOpenFilterDialog}>
        <DialogTitle>Filter</DialogTitle>
        <DialogContent dividers>
          <RadioGroup ref={radioFilterGroupRef}
            aria-label="filter"
            name="filter"
            value={filterRadioOption.value}
            onChange={handleFilterChange}>
            {filterOptions.map((option) => (
              <FormControlLabel value={option.value}
                key={option.value}
                control={<Radio />}
                label={option.label}/>
            ))}
          </RadioGroup>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCancel}>
            Cancel
          </Button>
          <Button onClick={handleOk}>Apply</Button>
        </DialogActions>
      </Dialog>
      <Dialog sx={{'& .MuiDialog-paper': {width: '80%', maxHeight: 435}}}
        maxWidth="xs"
        TransitionProps={{onEntering: handleSortEntering}}
        open={isOpenSortDialog}>
        <DialogTitle>Sort</DialogTitle>
        <DialogContent dividers>
          <RadioGroup ref={radioSortGroupRef}
            aria-label="sort"
            name="sort"
            value={sortRadioOption.value}
            onChange={handleSortChange}>
            {SORT_OPTIONS.map((option) => (
              <FormControlLabel value={option.value}
                key={option.value}
                control={<Radio />}
                label={option.label}/>
            ))}
          </RadioGroup>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleSortCancel}>
            Cancel
          </Button>
          <Button onClick={handleSortOk}>Apply</Button>
        </DialogActions>
      </Dialog>
    </Layout>
  );
};

export const query = graphql`
  query {
    allStrapiCollege(sort: {name: ASC}) {
      nodes {
        name
        code
        city
        id
        address
        branches {
          name
          code
          id
        }
        college_type {
          name
          id
        }
      }
    }
    allStrapiCollegeType(sort: {name: ASC}) {
      nodes {
        name
        id
      }
    }
  }
`;

export default Colleges;
